header {
	position: fixed;

	display: flex;
	align-items: center;
	gap: 20px;

	width: 100%;

	padding: 10px 20px;

	background-color: white;

	box-sizing: border-box;
	box-shadow: 0px 3px 10px grey;

	z-index: 10;

	img {
		width: 32px;
		height: 32px;
	}

	h1 {
		font-size: 1.5rem;
	}
}